$primary-color: #2f80ed;
$secondary-color: #4f4f4f;
$light-gray: #e5e5e5;
$middle-gray: #efefef;

.login-container {
  position: absolute;
  width: 344px;
  height: 248px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  border-radius: 5px;
  background: $light-gray;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  padding: 20px;
  box-sizing: border-box;
}

.text {
  font-family: 'Roboto';
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  color: #000000;
  margin-bottom: 20px;
}

.login-form {
  display: flex;
  flex-direction: column;
}

.login-input {
  width: 92%;
  height: 40px;
  margin-bottom: 20px;
  margin-right: 26px;
  border: 1px solid $light-gray;
  border-radius: 5px;
  padding: 0 10px;
  font-family: 'Roboto';
  font-size: 14px;
  line-height: 16px;
}

.button-container {
  display: flex;
  justify-content: right;
}

.button {
  width: 124px;
  height: 40px;
  margin-left: 14px;
  background: $primary-color;
  color: #FFFFFF;
  border: none;
  border-radius: 5px;
  font-family: 'Roboto';
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  cursor: pointer;
  transition: background 0.3s ease;
}

.button:hover {
  background: darken($primary-color, 10%);
}

.registration {
  font-family: 'Roboto';
  font-size: 12px;
  line-height: 14px;
  text-align: center;
  color: $primary-color;
  margin-top: 10px;
}

